<template>
  <div>
    <div>
      <span
        class="tab"
        :class="{ activeTab: selectedTab === tab }"
        v-for="(tab, index) of tabs"
        :key="index"
        @click="selectedTab = tab"
        >{{ tab }}</span
      >
      <div v-show="selectedTab === 'Users'">
        <ul>
          <li class="d-flex" v-for="(user, index) of users" :key="index">
            <h4>{{ user.name }}</h4>
            <router-link
              class="button badge -fill-gradient"
              :to="{
                name: 'displayuser',
                params: {
                  id: user.id
                }
              }"
              >Details</router-link
            >
            <span class="button badge -fill-gradient" @click="deleteUser(user)"
              >Delete</span
            >
          </li>
        </ul>
      </div>

      <div v-show="selectedTab === 'Characters'">
        <ul>
          <li
            class="d-flex"
            v-for="(character, index) of characters"
            :key="index"
          >
            <h4>{{ character.name }}</h4>
            <router-link
              class="button badge -fill-gradient"
              :to="{
                name: 'displaycharacter',
                params: {
                  id: character.id
                }
              }"
              >Details</router-link
            >
            <span
              class="button badge -fill-gradient"
              @click="deleteCharacter(character)"
              >Delete</span
            >
          </li>
        </ul>
      </div>

      <div v-show="selectedTab === 'Snapshots'">
        <ul>
          <li
            class="d-flex"
            v-for="(snapshot, index) of snapshots"
            :key="index"
          >
            <h4>{{ snapshot.date }}</h4>
            <router-link
              class="button badge -fill-gradient"
              :to="{
                name: 'displaysnapshot',
                params: {
                  id: snapshot.id
                }
              }"
              >Details</router-link
            >
            <span
              class="button badge -fill-gradient"
              @click="deleteSnapshot(snapshot)"
              >Delete</span
            >
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    users: {
      type: Array
    },
    characters: {
      type: Array
    },
    snapshots: {
      type: Array
    }
  },
  data() {
    return {
      selectedTab: 'Users',
      tabs: ['Users', 'Characters', 'Snapshots']
    }
  },
  methods: {
    deleteUser(user) {
      this.$store
        .dispatch('deleteUser', user)
        .then(this.$store.dispatch('fetchDashboardUsers'))
    },
    deleteCharacter(character) {
      this.$store
        .dispatch('deleteCharacter', character)
        .then(this.$store.dispatch('fetchDashboardCharacters'))
    },
    deleteSnapshot(snapshot) {
      this.$store
        .dispatch('deleteSnapshot', snapshot)
        .then(this.$store.dispatch('fetchDashboardSnapshots'))
    }
  }
}
</script>

<style scoped>
.d-flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.tab {
  margin-left: 20px;
  cursor: pointer;
}

.button {
  text-decoration: none;
}

.activeTab {
  color: #16c0b0;
  text-decoration: underline;
}
</style>
